'use client'

import { INews } from "@/type/Types";
import styles from './News.module.scss'
import { useRouter } from "next/navigation";
import { format, parse } from "date-fns";
import { ru } from "date-fns/locale";
import axios from "axios";
import { useEffect, useState } from "react";
import { toast, ToastContainer } from "react-toastify";
import 'react-toastify/dist/ReactToastify.css';

interface NewsProps {
  news: INews[];
}

const NewsDashboardClient = ({ news }: NewsProps) => {
  const router = useRouter();
  const [roles, setRoles] = useState<string[]>([]);
  const [hasAdminAccess, setHasAdminAccess] = useState(false);
  const [loading, setLoading] = useState(true);

  const formatDate = (dateString: string) => {
    const date = parse(dateString, "dd MMMM yyyy", new Date());
    return format(date, "dd MMMM yyyy", { locale: ru });
  };

  useEffect(() => {
    
    const getUserRoles = async () => {
      try {
        const token = localStorage.getItem('access_token');
        
        if (!token) {
          throw new Error('Токен авторизации не найден');
        }
        
        const response = await axios.post(
          `http://localhost:9091/api/user/roles-and-permissions`, 
          {}, 
          {
            headers: {
              'Authorization': `Bearer ${token}`,
              'Content-Type': 'application/json'
            }
          }
        );
        
        const userRoles = response.data.roles || [];
        setRoles(userRoles);
        setHasAdminAccess(userRoles.includes('admin'));
        
      } catch (error) {
        console.error('Ошибка при получении ролей и разрешений:', error);
        setRoles([]);
        setHasAdminAccess(false);
      } finally {
        setLoading(false);
      }
    };

    getUserRoles();
  }, []);

  const handleNewsClick = (id: number) => {
    if (hasAdminAccess) {
      router.push(`/dashboard/news/${id}`, { scroll: false })
    } else {
      toast.error('У вас нет прав администратора для редактирования новостей');
    }
  };

  const truncateText = (text: string, maxLength: number) => {
    if (!text) return '';
    if (text.length <= maxLength) return text;
    return text.slice(0, maxLength) + '...';
  };

  return (
    <div className={styles.container}>
      <ToastContainer />
      <h1 className={styles.title}>Управление новостями</h1>
      
      {hasAdminAccess && (
        <div className={styles.adminActions}>
          <button 
            className={styles.addButton}
            onClick={() => router.push('/dashboard/addnew')}
          >
            Добавить новость
          </button>
        </div>
      )}
      
      <div className={styles.tableContainer}>
        <table className={styles.table}>
          <thead>
            <tr>
              <th>Изображение</th>
              <th>Заголовок</th>
              <th>Подзаголовок</th>
              <th>Описание</th>
              <th>Контент</th>
              <th>Дата</th>
              <th>Действия</th>
            </tr>
          </thead>
          <tbody>
            {news.map((newsItem) => (
              <tr key={newsItem.id} className={styles.tableRow}>
                <td className={styles.imageCell}>
                  <div 
                    className={styles.newsImage}
                    style={{
                      backgroundImage: `url(${newsItem.image_url})`,
                    }}
                  />
                </td>
                <td className={styles.titleCell}>
                  {truncateText(newsItem.title_one, 50)}
                </td>
                <td className={styles.titleCell}>
                  {truncateText(newsItem.title_two, 50)}
                </td>
                <td className={styles.descriptionCell}>
                  {truncateText(newsItem.description, 50)}
                </td>
                <td className={styles.descriptionCell}>
                  {truncateText(newsItem.content, 50)}
                </td>
                <td className={styles.dateCell}>
                  {formatDate(newsItem.date)}
                </td>
                <td className={styles.actionsCell}>
                  <button 
                    className={styles.actionButton}
                    onClick={() => handleNewsClick(newsItem.id)}
                    disabled={!hasAdminAccess}
                  >
                    Редактировать
                  </button>
                  {hasAdminAccess && (
                    <button className={`${styles.actionButton} ${styles.deleteButton}`}>
                      Удалить
                    </button>
                  )}
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
}

export default NewsDashboardClient;